<template>
  <div class="home">
    <!--  -->
      <div class="nav">
        <span v-if="cityAdress" @click="searchMap">{{cityAdress.city}}</span>
        <van-icon @click="searchMap" class="adress_icon" v-else name="location-o" />
        <van-search
        @click="searchshop"
         placeholder=""
         input-align="center"
       />
      </div>
    <!--  -->
    <!--  -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="#000000" >
     <van-swipe-item v-for="item in banner" :key="item.id"><img :src="item.image_url" alt=""></van-swipe-item>
    </van-swipe>
    <!--  -->
    <!--  -->
    <van-grid :column-num="5">
       <van-grid-item @click="bigshop(item)" v-for="item in channel" :key="item.id" :icon="item.icon_url" :text="item.name" />
     </van-grid>
     <!--  -->
     <!--  -->
     <div class="goods">
       <p class="goods_title"></p>
       <ul>
         <li @click="searchgoods(item)" v-for="item in goods" :key="item.id"><img :src="item.new_pic_url" alt="">
         <p>{{item.name}}</p>
         <p>{{item.floor_price}}</p>
         </li>
       </ul>
     </div>
    <!--  -->
    <!--  -->
    <div class="new_goods">
      <span></span>
      <a @click="gonewgoods" href="javascript:;"></a>
    </div>
    <!--  -->
    <!--  -->
  <swiper :options="swiper_tuijian">
      <swiper-slide @click.native="goshopcard(item)" class="tuijian" v-for="item in newGoods" :key="item.id"><img :src="item.list_pic_url">
      <p>{{item.name}}</p>
      <p>{{item.goods_brief}}</p>
      <p>{{item.retail_price}}</p>
      </swiper-slide>
       <div class="swiper-scrollbar" slot="scrollbar"></div>
    </swiper>
    <!---->
    
    <!--  -->
    <!--  -->
     <div class="t_goods">
      <span>'</span>
      <a @click="gogoods" href="javascript:;"></a>
    </div>
   <!--  -->
   <!--  -->
   <swiper :options="swiper_tuijian">
      <swiper-slide @click.native="goshopcard(item)" class="tuijian" v-for="item in hotGoods" :key="item.id">
      <img :src="item.list_pic_url">
      <p>{{item.name}}</p>
      <p>{{item.goods_brief}}</p>
      <p>{{item.retail_price}}</p>
      </swiper-slide>
     <div class="swiper-scrollbar" slot="scrollbar"></div>
    </swiper>

    <!--  -->
    <!--  -->
    <div class="topic">
      <div class="t_title">
      <span></span><span class="icon"></span>
      </div>
  <!--  -->

  
    <swiper :options="swiper_topic">
      <swiper-slide @click.native="catedel(item)"  v-for="item in topicList" :key="item.id">
      <img :src="item.item_pic_url">
      <div class="price"><span>{{item.title}}</span><span>{{item.price_info}}</span></div>
      <p>{{item.name}}</p>
      <p>{{item.subtitle}}</p>
      </swiper-slide>
       <div class="swiper-scrollbar" slot="scrollbar"></div>
    </swiper>
 


    </div>
    <!--  -->
    <!--  -->
    <div class="homework">
      <div class="homecat" v-for="item in newCategoryList" :key="item.id">
      <div class="home_title" >
        <span>{{item.name}}</span>
      </div>
      <ul class="clear">
        <li @click="goshopcard(value)" v-for="value in item.goodsList" :key="value.id">
          <img :src="value.list_pic_url" alt="">
          <p>{{value.name}}</p>
          <p>{{value.retail_price}}</p>
        </li>
        <li class="last" @click="more(item)">
          <p>{{item.name}}</p>
          <span></span>
        </li>
      </ul>
      </div>
    </div>
    <!--  -->
    <!-- tabbar -->
    <tabbar></tabbar>
    <!-- tabbar -->
  </div>
</template>
<script>
import tabbar from '../../components/Tabbar.vue'
import {mapState} from 'vuex'
import {Swiper,SwiperSlide,directive} from 'vue-awesome-swiper'
import'swiper/swiper-bundle.css'
importSwiper2,{Navigation,Scrollbar}from'swiper';
Swiper2.use([Navigation,Scrollbar]);

import {home} from '@/api/home/index.js'
export default {
  data() {
    return {
      swiper_tuijian: {
      slidesPerView: 2.5,
        scrollbar: {
        el: '.swiper-scrollbar',
        hide: true,
      },
     
      },
      swiper_topic: {
      slidesPerView: 1.3,
        spaceBetween: 15,
          scrollbar: {
        el: '.swiper-scrollbar',
        hide: true,
      },
        
      },
      //
      banner:[],
      //
      channel:[],
      //
      goods:[],
      //
      newGoods:[],
      //
      hotGoods:[],
      //
      topicList:[],
      //
      newCategoryList:[]
    }
  },
  methods: {
    //
    catedel(item) {
      this.$router.push({path:'/catedelc',query:{
        item:JSON.stringify(item),
        topicList:JSON.stringify(this.topicList)
      }})
    },
    //
    goshopcard(item){
      // category/goshop?id=1006002
      this.$router.push({path:'/category/goshop',query:{
        id:item.id
      }})
    },
    //goods
    searchgoods(item) {
      console.log(item);
      this.$router.push({path:'/goodslist',query:{
        item:JSON.stringify(item.id)
      }})
    
    },
    //
    bigshop(value) {
      this.$router.push({path:'/home/Categorylist',query:{
        id:JSON.stringify(value.id)
      }})
    },
    //
    searchshop() {
      this.$router.push({path:'/home/searchshop'})
    },
    //
    getData() {
      home().then(res=> {
        console.log(res);
        this.banner = res.banner
        this.channel = res.channel
        this.goods = res.brandList
        this.newGoods = res.newGoods
        this.hotGoods = res.hotGoods
        this.topicList = res.topicList
        this.newCategoryList = res.newCategoryList
      })
    },
    //
    searchMap() {
      this.$router.push({path:'/home/search'})
    },
    //
    gonewgoods(){
      this.$router.push({path:'/home/newsgoods',query:{
        isnew:1
      }})
    },
    gogoods(){
      this.$router.push({path:'/home/newsgoods',query:{
        isnew:0
      }})
    },
    //
    more(value) {
       this.$router.push({path:'/home/Categorylist',query:{
        id:JSON.stringify(value.id)
      }})
    }
  },
  created() {
    this.getData()
  },
  computed: {
    ...mapState(['cityAdress'])
  },
  components:{
    tabbar,Swiper,SwiperSlide
  },
  beforeRouteLeave (to, from, next) {
    this.$store.commit('commitRouter',from.path)
    next()
  }
 
}
</script>
<style lang="scss">
.my-swipe {
  margin-top: 59.09px;
}
.clear::after {
  content: '';
  display: block;
  clear: both;
  visibility: hidden;
}
.home {
  overflow: hidden;
  height: 100%;
  width: 100vw;
  background-color: #F4F4F4;
}
  .nav {
    position: fixed;
    z-index: 999;
    width: 100%;
    background-color: #fff;
    display: flex;
    align-items: center;
    padding: 0 20px;


  .adress_icon  {
    font-size:16px;
    animation: move 2s infinite;
  }

  @keyframes move {
      0% {
      transform: translateY(-8px);
      }

      50%{
      transform: translateY(8px);
      }

      100% {
      transform: translateY(-8px);
      }

  }
    span {
      display: block;
      width: 50px;
      font-weight: 400;
      font-size: 14px;
      color: #000000;

    }
   .van-field__control--center {
     text-align: left;
     flex: 1;
   }
     .van-search {
       width: 100%;
     }
  }

  .my-swipe img {
    width: 100%;
  }

  .van-swipe__indicator {
    background-color: rgb(151, 148, 148);
  }
  .goods {
   margin-top: 10px;
   background-color: #fff;
   text-align: center;

   .goods_title {
     padding: 20px 0;
   }

   ul {
     display: flex;
     padding: 0 5px;
     justify-content: space-between;
     flex-wrap: wrap;
   }

    ul li {
      width: 49%;
      position: relative;

      p:nth-of-type(1) {
        position: absolute;
        top: 3px;
        left: 5px;
      }
       p:nth-of-type(2) {
        position: absolute;
        top: 20px;
        left: 5px;
        font-size: 13px;
      }
    }

    img {
      width: 100%;
    }
  }

  .new_goods ,.t_goods{
    margin-top: 5px;
    height: 144px;
    width: 100%;
    background-color: #F1F7FD;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    span {
      color: #8c9bae;
      font-size: 16px;
      display: block;
      padding: 10px;
    }
    a {
      display: block;
      padding: 6px 23px;
      color: #8c9bae;
      background-color: #d8e4f0;
    }
  }
 
    .swiper-scrollbar {
      height: 4px !important;
    }
  .tuijian{
    text-align: center;
   img {
    width: 100%;
  }

      p:nth-of-type(1) {
        width: 100px;
        font-weight: 700;
        font-size: 16px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin: 0 auto;
      }
       p:nth-of-type(2) {
         margin: 0 auto;
        width: 80px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-right: 45px;
        color: #8a8a8a;
        font-size: 13px;
        margin-top: 10px;
      }
       p:nth-of-type(3) {
        color: #9c3232;
        margin-right: 80px;
      }
  }
  .t_goods {
    background: url('data:image/png;base64,') no-repeat top center;

    span {
      color: #b1a279;
    }
    a {
      color: #b1a279;
      background-color: #f4e9cb;
    }
  }
  .topic {
    margin-top: 20px;
    padding:0  10px;
    height: fit-content;
    .t_title {
      height: 20px;
      line-height: 20px;
      text-align: center;
      font-size: 12px;
      margin-bottom: 20px;
   .icon {
     display: inline-block;
     margin-left: 10px;
     height: 16px;
     width: 16px;
     background: url('data:image/png;base64,') no-repeat top center;
     background-size: 100%;
     vertical-align: middle;
     margin-top: -4px;
   }
    }
  
    img {
      width: 100%;
      height: 171px;
    }
    .price {
      display: flex;
      justify-content: space-between;
     span:nth-of-type(1) {
       font-weight: 700;
       font-size:16px;
     }
     span:nth-of-type(2) {
      color: #9c3232;
       font-size: .32rem;
     }
    }
    p{
      color: #8a8a8a;
    }
  
  }

  .homework {
    margin-top: 30px;
    .home_title {
      text-align: center;
      font-size: 12px;
    }

    .homecat {
      margin-top: 20px;
    }
    ul {
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
      margin-top: 10px;
    }
  
    ul li {
      width: 48%;
      background-color: #fff;
     
      img {
        width: 100%;
      }

      p:nth-of-type(1) {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #333;
        font-size: 12px;
      }
       p:nth-of-type(2) {
       color: #9c3232;
      }

    }
      .last {
        text-align: center;
        p{
          margin-top: 45px;
          text-align: center;
          color: #333 !important;
          font-size: 18px !important;
        }
        span {
          height: 50px;
          width: 50px;
          display: inline-block;
          background: url('data:image/png;base64,') no-repeat top center;
          background-size: 100%;
        }
      }

  }
</style>