import request from '@/request.js'


// 
function home(data) {
    return request({
        url: "/index/index",
        data
    })
}

//
function category(data) {
    return request({
        url: "/category/categoryNav",
        data
    })
}

//id
function currentaction(data) {
    return request({
        url: "/goods/goodsList",
        data
    })
}


export {
    home,
    category,
    currentaction
}